<div class="cvContacts"><h3 class="cvContacts__title">CONTACT ME</h3><div class="cvContacts__container"><form class="cvContacts__form" @submit.prevent="onSubmit"><p class="cvContacts__wrapper"><input class="cvContacts__input" type="text" id="name" placeholder="First Name" v-model="name"><input class="cvContacts__input" type="email" id="email" placeholder="Email" v-model="email"></p><p class="cvContacts__wrapper"><input class="cvContacts__input" type="text" id="phone" placeholder="Phone" v-model="phone"></p><p class="cvContacts__wrapper"><textarea class="cvContacts__addText" id="message" placeholder="Tell Me Something..." v-model="message"></textarea></p><button class="cvContacts__submit" type="submit">SEND MESSAGE</button></form><div class="cvContacts__contacts"><div class="cvContacts__name">Harutyun Amirkhanyan</div><span class="cvContacts__proff">Front-End Developer</span><div class="cvContacts__block"><span class="cvContacts__desc">Tel:</span><span class="cvContacts__info">+374 (77) 166 744</span></div><div class="cvContacts__block"><span class="cvContacts__desc">Email:</span><span class="cvContacts__info">har.amirkhanyan@gmail.com</span></div><div class="cvContacts__contactsBlock"><a class="cvContacts__contactsWrapper" v-for="item in contacts" :key="item.id" :title="item.title" :href="item.href" target="_blank"> <img class="cvContacts__img" :src="item.img" loading="lazy" :alt="item.alt"></a></div></div></div></div>