<div class="cvNavbar"><div class="cvNavbar__imgWrapper"><img class="cvNavbar__img" :src="logo" loading="lazy" alt="logo"></div><h2 class="cvNavbar__title">{{aboutTitle}}</h2><p class="cvNavbar__text">{{aboutText}}</p><h2 class="cvNavbar__title">{{expTitle}}</h2><p class="cvNavbar__text">{{expText}}</p><h2 class="cvNavbar__title">{{contactsTitle}}</h2><div class="cvNavbar__contacts"><a class="cvNavbar__contactsWrapper" v-for="item in contacts" :key="item.id" :title="item.title" :href="item.href" target="_blank"> <img class="cvNavbar__img" :src="item.img" loading="lazy" :alt="item.alt"></a></div></div>