<div class="cvHeaderNav"> <div class="cvHeaderNav__container"><div class="cvHeaderNav__wrapper"><div class="cvHeaderNav__terminal"><h1 class="cvHeaderNav__name">{{name}}</h1></div><ul class="cvHeaderNav__menu"><li class="cvHeaderNav__item" v-for="item in nav" :key="item.id" @click="$scrollTo('.'+item.id)">{{item.text}}</li></ul><div class="cvHeaderNav__imgWrapper" @click.prevent="openSidebar"><img class="cvHeaderNav__img" :src="burgerLogo" alt="burger" loading="lazy"></div></div></div><span class="cvHeaderNav__line"><span class="cvHeaderNav__scrollLine"></span></span><CvSidebar><ul class="cvHeaderNav__menuSidebar"><li class="cvHeaderNav__itemSidebar" v-for="item in nav" :key="item.id" @click="$scrollTo('.'+item.id)">{{item.text}}</li></ul></CvSidebar></div>