<div class="cvSlider"><h2 class="cvSlider__title">My portfolio</h2><div class="cvSlider__content" v-if="true"><a class="cvSlider__imgWrapper" v-for="item in slideInfo" :key="item.id" :href="item.url" target="_blank"><img class="cvSlider__img" :src="item.img" loading="lazy" alt="alt"></a></div><Carousel class="cvSlider__carousel" v-else :items-to-show="1" :wrap-around="true" :touchDrag="false" ref="myCarousel"><Slide class="cvSlider__slide" v-for="item in slideInfo" :key="item.id"><h3 class="cvSlider__slide">{{item.title}}</h3><a class="cvSlider__imgWrapper" :href="item.url" target="_blank"><img class="cvSlider__img" :src="item.img" loading="lazy" alt="alt"></a></Slide><template class="cvSlider__btns" #addons><div class="cvSlider__btnsWrapper"><button class="cvSlider__prev" @click="myCarousel.prev()"></button><button class="cvSlider__next" @click="myCarousel.next()"></button></div></template></Carousel></div>